						{name:'选项三'}
					]
				},
				list:[
					{
						title:'好手机',
						titlepic:'/static/images/s6.jpg',
						desc:'好手机/漂亮/手感好/功能全面',
						pprice:1000,
						comment_num:10000,
						good_num:'98%'
					},
					{
